import * as Yup from "yup";

export const contactValidationSchema = Yup.object().shape({
  fullname: Yup.string().required("      "),
  email: Yup.string()
    .email("   ")
    .required("    "),
  subject: Yup.string().required("    "),
  message: Yup.string().required("    "),
  recaptcha: Yup.string().required("   "),
});
